body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100vw;
  max-width: 100%;
  background-color: rgb(250, 250, 250);
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-wrapper {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    transition: 0.5s;
}
.app-wrapper-transition {
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.5s;
}
.app-lower {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    height: calc(50% - 50px);
}
.fade-in {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
    opacity: 0;
    transition: 0.5s;
}
.fade-in-backswing {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
    transition: 0.5s;
}
.stretch {
    width: 0 !important;
    opacity: 0;
    transition: 0.5s;
}
.stretch-backswing {
    opacity: 1;
    transition: 0.5s;
}
.header {
    position: fixed;
    width: 100%;
    height: 25px;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: rgba(255, 255, 255, 0);
}
.header:hover {
    color: rgba(255, 255, 255, 1);
}

.header-item-active {
    width: 49%;
}
.header-item-inactive {
    width: 17%;
}
.header-item-transition {
    width: 25%;
}
.header-item-active, .header-item-inactive, .header-item-transition {
    transition: 0.5s;
    height: 100%;
    cursor: pointer;
    text-align: center;
    line-height: 25px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 25px;
    overflow: hidden;
}
@media only screen and (max-width: 800px) {
    .header-item-active, .header-item-inactive, .header-item-transition {
        font-size: 12px;
    }
}
.instruction-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    width: 100%;
    height: calc(50% - 50px);
    overflow: hidden;
}
.instruction-wrapper img {
    width: 100%;
    max-width: 100px;
    margin: 10px 0 20px 0;
}
.instruction-highlight {
    font-weight: 700;
    cursor: help;
    position: relative;
}
.instruction-highlight-info-active {
    opacity: 1;
    transition: 0.5s;
    transition-delay: 0.5s;
}
.instruction-highlight-info-inactive {
    opacity: 0;
    transition: 0.5s;
}
.instruction-highlight-info-active, .instruction-highlight-info-inactive {
    position: absolute;
    bottom: calc(100% + 50px);
    right: 0;
    width: 500px;
    font-weight: 500;
    color: black;
    cursor: auto;
    background-color: white;
    padding: 25px;
    border-radius: 25px;
}
.instruction-highlight-info-active-pointer-1, .instruction-highlight-info-active-pointer-2, .instruction-highlight-info-active-pointer-3 {
    opacity: 1;
    content: '';
    background-color: white;
    position: absolute;
}
.instruction-highlight-info-inactive-pointer-1, .instruction-highlight-info-inactive-pointer-2, .instruction-highlight-info-inactive-pointer-3 {
    opacity: 0;
    transition: 0s;
    width: 0;
    height: 0;
    position: absolute;
}
.instruction-highlight-info-active-pointer-1 {
    bottom: calc(100% + 5px);
    right: 60px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    transition: 0.1s;
}
.instruction-highlight-info-active-pointer-2 {
    bottom: calc(100% + 15px);
    right: 40px;
    width: 14px;
    height: 14px;
    border-radius: 7px;
    transition: 0.1s;
    transition-delay: 0.1s;
}
.instruction-highlight-info-active-pointer-3 {
    bottom: calc(100% + 25px);
    right: 60px;
    width: 20px;
    height: 20px;
    border-radius: 10px;
    transition: 0.1s;
    transition-delay: 0.3s;
}

@media only screen and (max-width: 800px) {
    .instruction-wrapper {
        font-size: 8px;
    }
    .instruction-highlight-info-active, .instruction-highlight-info-inactive {
        width: 200px;
    }
}
.app-form {
    width: 100%;
    height: 50px;
    max-width: 800px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.app-form-input {
    width: 70%;
    margin: 0 5%;
    height: 100%;
    font-size: 24px;
    border-radius: 25px;
    border: 4px solid black;
    padding: 0 25px;
    background-color: rgba(0, 0, 0, 0);
}
.app-form-input:focus {
    outline-width: 0;
}
.app-form-submit {
    width: 20%;
    height: 100%;
    border: 4px solid black;
    margin: 0;
    font-size: 24px;
    border-radius: 25px;
    font-weight: 700;
    cursor: pointer;
    box-sizing: content-box;
    -moz-box-sizing: content-box;
    background-color: rgba(0, 0, 0, 0);
}
.app-form-submit:focus {
    outline-width: 0;
}
.app-form-submit:hover {
    border: 4px solid red;
    background-color: red;
    color: white;
}
